
.map {
    width: 100vw;
    height: 100vh;
}

#parkiraj-me-btn {
    position: absolute;
    left: 20px;
    bottom: 175px;
    z-index: 999;
    border-radius: 12px;
}



#center-me {
    position: absolute;
    left: 20px;
    bottom: 100px;
    z-index: 999;
    width: 65px;
    height: 65px;
}

.fillParent {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(127, 255, 212, 0.51);
}

.fillParent:hover {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(158, 255, 223, 0.66);
    cursor: pointer;
    border-style: solid;
    border-width: 3px;
    border-color: rgba(255, 255, 255, 0.5);
}


.new-parking-marker:hover{
    cursor: move;
}

.marker-btns {
    margin-bottom: 10px;

}
.center {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.center2 {
    display: flow;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.center1 {
    display: flex;
    align-items: center;
    justify-content: center;
}


.reg-btn {
    margin: 5px;
    display: compact;
}

.reg-choose {
    margin: 5px;
    padding: 5px;

    border-color: rgba(0,0,0,0.19);
    display: grid;
    justify-content: center;
    align-items: center;
    width: 200px;
}


.inline {
    display: inline;
}



.right {
    position: relative;
    float: right;
}

.reservation {
    display: flow-root;
    align-items: center;
    justify-content: center;
}

